html,
body,
ul,
ol {
  margin: 0;
  padding: 0; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  width: 100%;
  height: 100%; }

@font-face {
  font-family: "Roboto";
  src: url("./assets/fonts/roboto/Roboto-Regular.ttf"); }

body {
  margin: 0;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4fb !important; }

#root {
  width: 100%;
  height: 100%; }

html::-webkit-scrollbar {
  display: none; }

html::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #e6221a, #ff125b); }

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background: #F5F5F5; }

html {
  scrollbar-color: #e61f1d #fff;
  scrollbar-width: 2px; }

.color-createtemplate {
  color: #666666; }

.margin-top-createtemplate {
  margin-top: 10px; }

.CreateTurnTemplateHeader {
  margin: 10px 0;
  margin-top: 21px;
  width: 65%;
  height: 26px;
  overflow: hidden; }
  .CreateTurnTemplateHeader .CreateTurnTemplateHeaderText {
    padding-left: 20px;
    color: #666666;
    font-weight: bold;
    opacity: 0.7; }

.headercreatetemplate {
  width: 100%; }
  .headercreatetemplate .Temp-Station {
    display: flex;
    width: 100%;
    padding: 17px 0px 10px 0px; }
    .headercreatetemplate .Temp-Station .Temp {
      width: 50%; }
    .headercreatetemplate .Temp-Station .station {
      width: 50%;
      margin-left: 15px; }
  .headercreatetemplate .Turn-planned-equptype-fleetcat {
    display: flex;
    margin-bottom: 23px; }
    .headercreatetemplate .Turn-planned-equptype-fleetcat .Turn {
      width: 35%; }
    .headercreatetemplate .Turn-planned-equptype-fleetcat .planned {
      width: 20%;
      margin: 0px 16px 0px 13px; }
    .headercreatetemplate .Turn-planned-equptype-fleetcat .equptype {
      width: 20%;
      margin: 0px 17px 0px 13px; }
    .headercreatetemplate .Turn-planned-equptype-fleetcat .fleetcat {
      width: 25%; }
